 .modal{
    display: none;
    position: absolute;
    top: 0;
    left: -300px;
    width: 100%;
    height: 100%;
   z-index: -100;
     transition: all 0.7s ease-in-out;

    display: flex;
    justify-content: center;
    align-items: center;
 }
 .modal.open{
    display: flex;
    z-index: 100;
    transform: translateX(300px);
    opacity: 1;
   background-color: white;
    transition: all 0.7s ease-in-out;
   
 }
   